@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
.App {
  display: flex;
  width: 100%;
  align-items: stretch;
}

body {
  font-family: "Poppins", sans-serif;
}

a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s;
}

/* ---------------------------------------------------
  SIDEBAR STYLE
----------------------------------------------------- */
.sidebar {
  min-width: 220px;
  max-width: 220px;
  height: 100%;
  background: #000000;
  color: #fff;
  margin-left: -250px;
  transition: all 0.5s;
}

.sidebar.is-open {
  margin-left: 0;
  transition: 0.5s;
}

.sidebar-header {
  background: #000000;
}

.sidebar-header h4 {
  color: #fff;
  padding: 1.5em;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.menu-open {
  background: #000000;
}

.nav-item:hover {
  color: #fff;
  border: 2px solid #fff;
  border-radius: 15px;
  padding: 12px;
}

.nav-item {
  padding: 10px;
  margin-top: 5px;
}

.items-menu {
  color: #fff;
  background: #000000;
}

li a.dropdown-toggle::after {
  display: inline-flex;
  position: relative;
  left: 60%;
  top: 10%;
}

.sidebar-header > span {
  position: relative;
  float: right;
  margin: 0.5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}
.side-menu {
  height: calc(100vh - 130px);
  overflow-y: scroll;
}

.side-menu::-webkit-scrollbar {
  width: 10px;
}

.side-menu::-webkit-scrollbar-thumb {
  background: #5466b3;
  border-radius: 3px;
}

.side-menu::-webkit-scrollbar-thumb:hover {
  background: #000000;
}

/* ---------------------------------------------------
  CONTENT STYLE
----------------------------------------------------- */
.content {
  padding: 25px;
  margin-left: 10;
  height: 100vh;
}

/* ---------------------------------------------------
  NAVBAR
----------------------------------------------------- */

.navbar-button {
  background-color: rgb(240, 239, 239);
  color: #000;
}

/* ---------------------------------------------------
  LOGIN
----------------------------------------------------- */

.center-vertical {
  margin-left: 50%;
  transform: translateX(-50%);
}

.logo-login {
  margin-left: 53%;
  transform: translateX(-50%);
  color: rgb(0, 0, 0);
  padding-bottom: 5%;
}

.login {
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  max-width: 35%;
  padding: 2%;
  border: 2px solid black;
  border-radius: 7%;
  background-color: rgb(216, 216, 216);
}

.container-login {
  width: 100%;
  height: 100%;
  position: fixed;
}

.cadaster {
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  max-width: 35%;
  padding: 2%;
  border: 2px solid black;
  border-radius: 7%;
  background-color: rgb(216, 216, 216);
}

.container-cadaster {
  box-shadow: 20px 20px black;
  width: 100%;
  height: 100%;
  position: fixed;
}

/* #background {
  opacity: 0.7;
} */

@media only screen and (max-width: 1100px) {
  .logo-login {
    margin-left: 40%;
    transform: translateX(-10%);
  }

  .login {
    max-width: 60%;
  }
}


@media only screen and (max-width: 800px) {
  .logo-login {
    margin-left: 40%;
    transform: translateX(-10%);
  }

  .login {
    max-width: 60%;
  }
}

@media only screen and (max-width: 500px) {
  body {
    overflow: hidden;
  }

  .content.is-open {
    margin-left: 100%;
  }

  .sidebar.is-open {
    min-width: 100%;
    max-width: 100%;
    margin-left: 0;
    transition: all 0.5s, height 0s;
  }

  .sidebar.is-open > .sidebar-header span {
    display: unset;
  }

  li a.dropdown-toggle::after {
    display: inline-block;
    position: relative;
    left: 68%;
  }

  .logo-login {
    margin-left: 30%;
    transform: translateX(-19%);
  }

  .login {
    max-width: 70%;
  }
}



